.examples-product {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  @media (min-width: @tablet-width) {
    flex-direction: row;
  }
  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-bottom: 69px;
  }

  &__container {
    width: 100%;
  }

  &__description {
    @media (min-width: @tablet-width) {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
    }
  }

  &__title {
    color: @black;
    font-size: 36px;
    line-height: 40px;
    margin-top: 25px;
    margin-bottom: 36px;

    @media (min-width: @tablet-width) {
      font-size: 60px;
      line-height: 60px;
      margin-bottom: 47px;
      margin-top: 44px;
    }
    @media (min-width: @desktop-width) {
      font-size: 58px;
      margin-top: 47px;
    }
  }

  &__text {
    color: @dark-grey-second;
    font-size: 14px;
    line-height: 18px;
    font-family: "Arial";
    font-weight: @font-weight-default;
    @media (min-width: @tablet-width) {
      font-size: 16px;
      line-height: 24px;
      margin-bottom: 53px;
      padding-right: 10px;
    }
    @media (min-width: @desktop-width) {
      margin-top: 16%;
      margin-bottom: 0;
      width: 72%;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    @media (min-width: @tablet-width) {
      width: 51.7%;
      padding-bottom: 76px;
    }
    @media (min-width: @desktop-width) {
      width: 60%;
      padding-bottom: 0;
      align-items: flex-start;
      margin-top: 11%;
    }
  }

  &__items {
    position: relative;
    width: 45%;
    border: 1px solid #CDCDCD;
    box-sizing: border-box;
    border-radius: 3px;
    padding-left: 20px;
    padding-right: 20px;
    text-align: center;
    @media (min-width: @tablet-width) {
      min-width: 162px;
      padding: 0;
    }
  }

  &__item-price,
  &__item-days {
    margin-top: 14px;
    margin-bottom: 16px;
    font-size: 24px;
    line-height: 24px;
    color: @black;
    text-transform: uppercase;
    @media (min-width: @tablet-width) {
      font-size: 30px;
      line-height: 37px;
    }
  }

  &__item-text {
    position: absolute;
    bottom: -51%;
    left: 19%;
    width: 61%;
    background: #F2F2F2;
    font-family: "Arial";
    font-size: 12px;
    line-height: 12px;
    color: @dark-grey-second;
    @media (min-width: @tablet-width) {
      font-size: 14px;
      line-height: 14px;
      bottom: -36%;
      left: 6%;
      width: 87%;
      background: white;
    }
    @media (min-width: @desktop-width) {
      background: #F2F2F2;
    }
  }

  &__cost {
    margin-top: 34px;
    margin-bottom: 44px;
    font-size: 14px;
    line-height: 20px;
    color: @black;
    text-align: center;
    text-transform: uppercase;
    @media (min-width: @tablet-width) {
      line-height: 28px;
      font-size: 20px;
      text-align: right;
      margin-top: 8px;
      width: 25%;
      padding-left: 23%;
      margin-bottom: 0;
    }
    @media (min-width: @desktop-width) {
      width: 100%;
      padding-left: 0;
      text-align: left;
      height: 8%;
      margin-top: 10%;
      span {
        padding-left: 59px;
      }
    }
  }

  &--before {
    .examples-product__cat-before {
      picture {
        transition: right 0.5s ease-in-out;
      }

      img {
        transition: 0.5s ease-in-out;
        display: block;
        margin: 0 auto;
        height: 228px;
        @media (min-width: @tablet-width) {
          height: 529px;
        }
      }
    }

    .examples-product__cat-after {
      img {
        margin: 0 auto;
        display: none;
      }
    }

    .before-after__white-circle {
      transition: 0.5s ease-in-out;
      left: 0;
    }
  }

  &--after {
    .examples-product__cat-after {
      picture {
        transition: left 0.5s ease-in-out;
      }

      img {
        transition: 0.5s ease-in-out;
        display: block;
        margin: 0 auto;
        height: 228px;
        @media (min-width: @tablet-width) {
          height: 529px;
        }
      }
    }

    .examples-product__cat-before {
      img {
        margin: 0 auto;
        display: none;
      }
    }

    .before-after__white-circle {
      transition: 0.5s ease-in-out;
      left: 96%;
    }
  }
}
